<template>
  <div>
    <mrc_ess_table v-if="$route.name=='staffGroup'"
                   :headers='headers'
                   pageName='addStaffGroup'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['organization_info']"
                   :items='desserts'>

      <template v-slot:top>
        <v-card-title class="cardtitle"
                      style="height:60px">
          <mrc_ess_searchInput :isShow='true'
                               style="position:absolute;right:10px;top:18px;"></mrc_ess_searchInput>
        </v-card-title>
      </template>

      <template v-slot:item.organization_info='{item}'>
        <v-chip v-for="item1 in item.organization_info"
                :key="item1.uuid">
          {{INT(item1.organization_names)}}
        </v-chip>
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import {
  getStaffCompanyGroupList
  , addCompanyGroup
  , detailCompanyGroup
  , deleteCompanyGroup
} from '@/service/api/staffmsg.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      stepIndex: 1,
      dialog: false,
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      desserts: [],
    };
  },
  computed: {
    stepList () {
      return [
        {
          name: this.$t('label_Importing_files')
        },
        {
          name: this.$t('label_result')
        },
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View') },
        { name: this.$t('label_Edit') },
        { name: this.$t('label_delete') },
      ]
    },
    headers () {
      return [
        { text: this.$t('label_groupName'), align: 'start', value: 'groupName', sortable: false },
        { text: this.$t('label_member'), value: 'organization_info', sortable: false, align: 'start', },
        { text: '', value: 'actions', sortable: false, align: 'end', width: '100' },
      ]
    },
  },
  created () { },
  watch: {
    $route (v) {
      if (v.name == 'staffGroup') {
        this.getlist()
      }
    }
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  mounted () {
    this.getlist()
  },

  methods: {
    changeIndex (v) {
      if (v == 0) {
        this.dialog = false
      }
      else if (v == 3) { //提交代码逻辑

      }
      else {
        this.stepIndex = v
      }
    },
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      let res = await getStaffCompanyGroupList({
        cpage: this.options.cpage,
        pagesize: this.options.pagesize,
        company_uuid: this.$store.state.currentCompany
      })
      if (res.code == 200) {
        res = res.data
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_subPage_name', item.groupName)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailStaffGroup'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          this.$store.commit('set_subPage_name', item.groupName)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailStaffGroup'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteCompanyGroup({ uuids: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                if (this.desserts.length = 1) this.options.cpage--
                this.getlist()
              }
            })
          })
          break
      }
    },
    success () { },
    cancel () { }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
.dowanTemplate {
  position: absolute;
  right: 180px;
  top: -50px;
}
</style>
